import React, { useState } from 'react'
import {faDiscord,faGoogle} from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'

export default function HomePage() {

    const [disActive, setDiscordActive] = useState(false)
    const [gooActive, setGoogleActive] = useState(false)

    return (
        <div className="homepage">
            <header className="hero">
                <h1 className="hero-text">
                    Hi, I am <span>LxtteDev.</span>
                </h1>
                <p className="h-sub-text">
                    Hi! Im <span>LxtteDev</span>. The owner and founder of <a href="https://roblox.com/groups/10029720/Haze-Studios/" target="_blank">Haze Studio's</a>, a Roblox group that creates games using TypeScript.
                </p>
                <div className="icons">
                    <div className="icon-holder">
                        <FontAwesomeIcon icon={faDiscord} className="icon" onMouseDown={() => setDiscordActive(!disActive)}/> <span className={disActive ? 'active' : ''}>Lxtte#2161</span>
                    </div>
                    <div className="icon-holder">
                        <FontAwesomeIcon icon={faGoogle} className="icon"onMouseDown={() => setGoogleActive(!gooActive)} /> <span className={gooActive ? 'active' : ''}>lxttedev@gmail.com</span>
                    </div>
                </div>
            </header>
        </div>
    )
}

//<Link to="https://roblox.com/">Haze Studio's</Link>