import React from 'react'
import about from "../img/about.png"

export default function ImageSection() {
    return (
        <div className="imagesection">
            <div className="img">
                <img src={about} alt="" />    
            </div>
            <div className="about-info">
                <h4>I am <span>LxtteDev</span></h4>
                <p>
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Aspernatur fuga doloribus placeat totam amet provident. Expedita officia sapiente ipsum eaque?
                </p>
                <div className="about-details">
                    <div className="details">
                    </div>
                </div>
            </div>
        </div>
    )
}